







import { Component, Vue, Prop } from 'vue-property-decorator';
import { Icon } from 'mand-mobile';

@Component({
    components: {
        [Icon.name]: Icon,
    },
})
export default class MKRight extends Vue {
    @Prop({ type: Object, default: () => ({})})
    private rightInfo: any;

    private goRight(): void {
        window.history.go(-1);
    }
}
