







import { Component, Vue } from 'vue-property-decorator';
@Component
export default class TimeTip extends Vue {
    private time: number = 3;
    private changeTime() {
        const timeFlag = setInterval(() => {
            this.time--;
            if (this.time === 0) {
                clearInterval(timeFlag);
            }
        }, 1000);
    }
    private go() {
        alert('1111');
    }
    private created() {
        this.changeTime();
    }
}
